.jumbotron {
  padding: 100px;
}

.list-group-item p {
  font-size: 16px;
}

.btn-container {
  float: right;
}

@media (max-width: 767px) {
  .btn-container {
    margin-bottom: 15px;
  }
}

.wrapper {
    background: #114357;
    height: 100%;
    position: absolute;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 0px;
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    overflow: auto;
    margin-right: auto; 
    margin-left: auto; 
    max-width: auto; 
    width: 100%; 
  }
  
.header {
    height: 200px;
    background-size: cover;
    background-position: center;
    position: relative;
    object-fit: fill;
    background-repeat: no-repeat;
    /* background-size: auto; */
    height: 150px;
  }
  
  .header::after {
    content: "";
    display: block;
    opacity: .1;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: black;
  }
  
  .header * {
    position: relative;
    z-index: 2;
    object-fit: contain;
  }
  
  .hero h1, .hero h2 {
    color: white;
  }
  
  .hero h1 {
    font-size: 60px;
    padding-top: 140px;
    margin-top: 0;
  }
  
.navbar {
  border-radius: 0;
}

